* {
  margin: 0;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

.section {
  background-color: #ae1332;
  margin: 0;
  padding: 0 32px;
}

body {
  color: #fff;
  background-color: #ae1332;
  font-family: DM Sans, sans-serif;
}

.list {
  padding: 0;
  list-style: none;
}

.link, a {
  text-decoration: none;
}

.header {
  width: 100%;
  background-color: #fff;
  padding: 16px;
}

.header-container {
  max-width: 1280px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-navbar-menu {
  text-align: left;
