.slide-bottom {
  animation: slide-bottom .5s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes slide-bottom {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(100px);
  }
}

.slide-fwd-right {
  animation: slide-fwd-right .45s linear alternate-reverse both;
}

@keyframes slide-fwd-right {
  0% {
    transform: translateZ(0)translateX(0);
  }

  100% {
    transform: translateZ(160px)translateX(100px);
  }
}

.slide-fwd-left {
  animation: slide-fwd-left .45s cubic-bezier(.25, .46, .45, .94) alternate-reverse both;
}

@keyframes slide-fwd-left {
  0% {
    transform: translateZ(0)translateX(0);
  }

  100% {
    transform: translateZ(160px)translateX(-100px);
  }
}

